
body,a,p,li,ul,h1,h2,h3,h4,h5,strong,div,img{
  font-size: 0.12rem; font-family: "微软雅黑",arial; margin: 0; padding: 0; border: none; text-decoration: none;
}
ul,li{
  list-style:none;
}
#app {
    height: 100%;
}
body {
    height: 100%;
}
html {
    height: 100%;
}
.wrap{
	overflow-x: hidden;
}
.nav{
	position:absolute; z-index:99; top:0.62rem; left: 0; width: 100%;
}
.nav a{
	display:block; height:0.42rem; border-bottom: 0.01rem solid #3c3434; line-height:0.42rem; padding-left: 0.2rem; color:#fff;
}
.nav .router-link-exact-active{
	color:#fff; background: #3a1113;
}
.layout{
   width: 2.3rem; padding-top: 0.1rem; overflow: hidden;
}
.layout span{
	font-size: 0.14rem; display: block; color: rgba(255,255,255,.8); text-align: center; line-height: 0.5rem;
}
.vux-drawer .actives{
  -webkit-transform:translate3d(2.3rem, 0px, 0px) !important; transform:translate3d(2.3rem, 0px, 0px) !important;
}
.vux-drawer .activer{
  -webkit-transform:translate3d(-2.3rem, 0px, 0px) !important; transform:translate3d(-2.3rem, 0px, 0px) !important;
}
.vux-drawer > .vux-drawer-content{
	 background: rgb(54, 37, 38);
}
.head{
	height:0.7rem; overflow: hidden; width: 100%; background: rgb(54, 37, 38); color: #fff;
}
.head p{
	font-size:0.18rem; font-weight: bold;
}
.head .logo{
	float:left; width:82%; padding:0.12rem 0 0 0.12rem; margin-bottom:0.12rem;
}
.head .logo strong{
	width:0.44rem; height:0.44rem; font-size:0.15rem; float:left; margin-right:0.1rem; margin-top:0.12rem;
}
.head .logo img{
	width:0.44rem; height:0.44rem; font-size:0.15rem; float:left; margin-right:0.1rem;
}
.head .icon{
	float:left; width:0.37rem; height:0.37rem; border-radius: 0.04rem; margin-top:0.1rem;
}
.head .icon svg{
	fill:#fff; margin:0.08rem 0rem; width:0.35rem; height:0.4rem; cursor:pointer; height: 0.3rem;
}
.title{
    text-align:center; margin:0.3rem 0;
}
.title p{
    font-size:0.2rem; line-height:0.2rem; color:#5a5a5a; letter-spacing:0.02rem;
}
.title img{
	width: 80%; margin-top: 0.1rem;
}
.page{
    margin:0.2rem 0;color:#5a5a5a; padding: 0.05rem 0; overflow:hidden; text-align:center; clear: both;
}
.page .num{
    border:0.01rem solid #FF9600; margin:0 0.02rem; padding:0 0.05rem;background: url('assets/pagerbg.jpg') center center repeat-x;
}
.page a{ 
    color:#FF6500;
}
.page .active{
    color:#FF6500;  border: 0.01rem solid #FF6500; background: #FFBE94;
}
.page .num:hover{
    color:#FF9600; ; background: #FFBE94;
}
  
/* 路由切换动画 */
.child-view {  
  width: 100%; height: 100%; -webkit-transition: all 0.5s cubic-bezier(.55,0,.1,1);  -webkit-transition: all .5s cubic-bezier(.55,0,.1,1);  transition: all .5s cubic-bezier(.55,0,.1,1);
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform; -webkit-transition: all 0.5s; transition: all 0.5s; height: 100%; position: absolute;  -webkit-backface-visibility: hidden;  backface-visibility: hidden;  -webkit-perspective: 1000;  perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0; -webkit-transform: translate3d(-100%, 0, 0); transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0; -webkit-transform: translate3d(100%, 0, 0); transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0; -webkit-transform: translate3d(100%, 0, 0); transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0; -webkit-transform: translate3d(-100%, 0, 0); transform: translate3d(-100%, 0, 0);
}



